<template>
  <div class="container">
    <h3>Users</h3>
    <div class="table-responsive-sm">
      <table class="table table-striped">
        <thead>
          <tr>
            <th>Id</th>
            <th>Username</th>
            <th>Mail</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" :key="user.id">
            <td>{{ user.id }}</td>
            <td>
              <a href="#" @click="seeUserClicked(user.id)">{{
                user.username
              }}</a>
            </td>
            <td>{{ user.email }}</td>
            <td></td>
            <td>
              <button class="btn btn-primary" @click="seeUserClicked(user.id)">
                <info-icon />
              </button>
              <button
                class="btn btn-danger"
                @click="deleteUserClicked(user.id)"
              >
                <trash-icon />
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import UserDataService from "../service/UserDataService";
import trashIcon from "@/components/TrashIcon.vue";
import infoIcon from "@/components/InfoIcon.vue";

export default {
  name: "Users",
  components: {
    trashIcon,
    infoIcon,
  },

  data() {
    return {
      users: [],
    };
  },

  methods: {
    async getUsers() {
      const response = await UserDataService.retrieveAllUsers();
      this.users = response.data;
    },
    seeUserClicked(id) {
      this.$router.push(`/users/${id}`);
    },
    async deleteUserClicked(id) {
      const response = await UserDataService.deleteUser(id);
      this.getUsers();
    },
  },

  created() {
    this.getUsers();
  },
};
</script>

<style scoped>
button {
  display: inline-flex;
  align-items: center;
  margin-right: 5px;
}
</style>